import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class ApiService {

	constructor(
		private http: HttpClient
	) { }

	getMeasurement(zipCode: string) {
		return this.http.get<{
			salesVolumeGrowthMoM: number,
			salesTranCntGrowthMoM: number,
			salesVolumeGrowthYoY: number,
			salesTranCntGrowthYoY: number,
			spendOutsideGeography: number,
			avgTransactionFrequencey: number
		}>("/api/visa/merchant-measurement?zip=" + zipCode)
	}



	// getMeasurement(zipCode: string) {
	// 	return {
	// 		salesVolumeGrowthMoM: +(Math.random() * 100 - 50).toFixed(4),
	// 		salesTranCntGrowthMoM: +(Math.random() * 100 - 50).toFixed(4),
	// 		salesVolumeGrowthYoY: +(Math.random() * 100 - 50).toFixed(4),
	// 		salesTranCntGrowthYoY: +(Math.random() * 100 - 50).toFixed(4),
	// 		spendOutsideGeography: +(Math.random() * 30).toFixed(2),
	// 		avgTransactionFrequencey: +(Math.random() * 10).toFixed(2)
	// 	}
  //
	// }

	getWalkability(lat: number, lon: number) {
		return this.http.get<{
			score: number
		}>("/api/walkscore?lat=" + lat + "&long=" + lon)
	}

	getDepositRates(balance:number, loanAmount: number, term: string, zipcode: string) {
		return this.http.get<{
			term: string,
			interestRate: number,
			accountBalanceTiers: string
		}>("/api/usbank/deposit-rates?balance=" + balance + "&loanAmount=" + loanAmount + "&term=" + term + "&zipcode=" + zipcode)
	}
}



